<template>
  <div class="asset-detail" v-loading="loading">
    <!--  -->
    <div class="back-link">
      <el-button @click="$router.back()" icon="el-icon-arrow-left" type="text"></el-button>
    </div>
    
    <!--  -->
    <div class="detail-header" v-if="asset">
      <div class="header-left">
        <h1 class="asset-name">
          <span class="asset-icon"><i class="el-icon-office-building"></i></span>
          {{ asset.name }}
        </h1>
        <div class="asset-meta">
          <el-tag :type="getAssetTypeTag(asset.type)" class="meta-tag">
            {{ getAssetTypeLabel(asset.type) }}
          </el-tag>
          <el-tag :type="getEnvironmentTag(asset.environment)" class="meta-tag">
            {{ getEnvironmentLabel(asset.environment) }}
          </el-tag>
          <span class="meta-date"> {{ formatDate(asset.createdAt) }}</span>
        </div>
        <p class="asset-description">{{ asset.description }}</p>
      </div>
      <div class="header-actions">
        <el-button type="primary" @click="goToEdit" class="action-btn">
          <i class="el-icon-edit"></i> 
        </el-button>
        <el-button type="danger" @click="confirmDelete" class="action-btn">
          <i class="el-icon-delete"></i> 
        </el-button>
      </div>
    </div>
    
    <!--  -->
    <div class="detail-content" v-if="asset">
      <el-tabs v-model="activeTab" type="border-card">
        <!--  -->
        <el-tab-pane label="" name="info">
          <el-card class="info-card">
            <div class="info-grid">
              <div class="info-item">
                <h3 class="info-label">ID</h3>
                <p class="info-value">{{ asset.id }}</p>
              </div>
              <div class="info-item">
                <h3 class="info-label">IP</h3>
                <p class="info-value">{{ asset.ipAddress || '' }}</p>
              </div>
              <div class="info-item">
                <h3 class="info-label"></h3>
                <p class="info-value">{{ asset.domain || '' }}</p>
              </div>
              <div class="info-item">
                <h3 class="info-label"></h3>
                <p class="info-value">{{ asset.operatingSystem || '' }}</p>
              </div>
              <div class="info-item">
                <h3 class="info-label"></h3>
                <p class="info-value">{{ asset.owner || '' }}</p>
              </div>
              <div class="info-item">
                <h3 class="info-label"></h3>
                <p class="info-value">{{ asset.department || '' }}</p>
              </div>
              <div class="info-item">
                <h3 class="info-label">MAC</h3>
                <p class="info-value">{{ asset.macAddress || '' }}</p>
              </div>
              <div class="info-item">
                <h3 class="info-label"></h3>
                <p class="info-value">{{ asset.ports ? asset.ports.join(', ') : '' }}</p>
              </div>
              <div class="info-item">
                <h3 class="info-label"></h3>
                <p class="info-value">{{ formatDate(asset.updatedAt) }}</p>
              </div>
            </div>
          </el-card>
          
          <!--  -->
          <el-card class="custom-attributes-card" v-if="asset.attributes && Object.keys(asset.attributes).length > 0">
            <template #header>
              <div class="card-header">
                <span></span>
              </div>
            </template>
            <el-table :data="attributesArray" style="width: 100%">
              <el-table-column prop="key" label="" width="200" />
              <el-table-column prop="value" label="" />
            </el-table>
          </el-card>
        </el-tab-pane>
        
        <!--  -->
        <el-tab-pane label="" name="vulnerabilities">
          <div class="tab-header">
            <h3 class="tab-title"> <el-badge :value="vulnerabilities.length" type="danger" /></h3>
            <el-button type="primary" size="small" @click="dialogVisible = true"></el-button>
          </div>
          
          <el-empty description="" v-if="vulnerabilities.length === 0"></el-empty>
          
          <el-table 
            v-else
            :data="vulnerabilities" 
            style="width: 100%"
            border
          >
            <el-table-column label="ID" prop="id" width="80" />
            <el-table-column label="" prop="title" min-width="150">
              <template #default="scope">
                <router-link :to="{ name: 'VulnDetail', params: { id: scope.row.id } }" class="vuln-link">
                  {{ scope.row.title }}
                </router-link>
              </template>
            </el-table-column>
            <el-table-column label="CVE" prop="cveId" width="120" />
            <el-table-column label="" prop="severity" width="100">
              <template #default="scope">
                <el-tag :type="getSeverityTag(scope.row.severity)">
                  {{ getSeverityLabel(scope.row.severity) }}
                </el-tag>
              </template>
            </el-table-column>
            <el-table-column label="" prop="status" width="100">
              <template #default="scope">
                <el-tag :type="getStatusTag(scope.row.status)">
                  {{ getStatusLabel(scope.row.status) }}
                </el-tag>
              </template>
            </el-table-column>
            <el-table-column label="" width="120">
              <template #default="scope">
                <el-button 
                  size="small" 
                  type="danger" 
                  plain
                  @click="confirmRemoveVulnerability(scope.row)"
                >
                  
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        
        <!--  -->
        <el-tab-pane label="" name="notes">
          <div class="tab-header">
            <h3 class="tab-title"></h3>
            <el-button type="primary" size="small" @click="showAddNoteDialog"></el-button>
          </div>
          
          <el-empty description="" v-if="!asset.notes || asset.notes.length === 0"></el-empty>
          
          <div v-else class="notes-list">
            <el-timeline>
              <el-timeline-item
                v-for="(note, index) in asset.notes"
                :key="index"
                :timestamp="formatDate(note.createdAt)"
                placement="top"
                color="#409EFF"
              >
                <el-card class="note-card">
                  <div class="note-content">{{ note.content }}</div>
                  <div class="note-footer">
                    <span class="note-author"> {{ note.createdBy.name || note.createdBy.username || '' }} </span>
                  </div>
                </el-card>
              </el-timeline-item>
            </el-timeline>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
    
    <!--  -->
    <el-dialog
      title=""
      v-model="dialogVisible"
      width="600px"
    >
      <el-form :model="searchForm" label-width="80px">
        <el-form-item label="">
          <el-input 
            v-model="searchForm.searchTerm" 
            placeholder="CVE"
            clearable
            @keyup.enter="searchVulnerabilities"
          >
            <template #append>
              <el-button icon="el-icon-search" @click="searchVulnerabilities"></el-button>
            </template>
          </el-input>
        </el-form-item>
      </el-form>
      
      <el-table
        v-loading="searchLoading"
        :data="searchResults"
        style="width: 100%"
        height="300"
        @row-click="handleVulnerabilitySelect"
      >
        <el-table-column type="selection" width="55" :selectable="isSelectable" />
        <el-table-column label="" prop="title" min-width="150" />
        <el-table-column label="CVE" prop="cveId" width="120" />
        <el-table-column label="" prop="severity" width="100">
          <template #default="scope">
            <el-tag :type="getSeverityTag(scope.row.severity)">
              {{ getSeverityLabel(scope.row.severity) }}
            </el-tag>
          </template>
        </el-table-column>
      </el-table>
      
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogVisible = false"></el-button>
          <el-button type="primary" @click="addSelectedVulnerabilities" :disabled="selectedVulnerabilities.length === 0">
             ({{ selectedVulnerabilities.length }})
          </el-button>
        </span>
      </template>
    </el-dialog>
    
    <!--  -->
    <el-dialog
      title=""
      v-model="noteDialogVisible"
      width="500px"
    >
      <el-form :model="noteForm" label-width="80px">
        <el-form-item label="" required>
          <el-input 
            v-model="noteForm.content" 
            type="textarea" 
            :rows="4"
            placeholder=""
          />
        </el-form-item>
      </el-form>
      
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="noteDialogVisible = false"></el-button>
          <el-button type="primary" @click="addAssetNote" :disabled="!noteForm.content"></el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { ref, reactive, onMounted, computed } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { ElMessageBox, ElMessage } from 'element-plus'
import assetApi from '@/api/assets'
import vulnerabilityApi from '@/api/vulnerabilities'
import { formatDate } from '@/utils/helpers'

export default {
  name: 'AssetDetail',
  
  setup() {
    const route = useRoute()
    const router = useRouter()
    const loading = ref(false)
    const asset = ref(null)
    const activeTab = ref('info')
    const vulnerabilities = ref([])
    
    // 
    const dialogVisible = ref(false)
    const searchForm = reactive({
      searchTerm: ''
    })
    const searchLoading = ref(false)
    const searchResults = ref([])
    const selectedVulnerabilities = ref([])
    
    // 
    const noteDialogVisible = ref(false)
    const noteForm = reactive({
      content: ''
    })
    
    // 
    const fetchAssetDetails = async () => {
      const assetId = route.params.id
      if (!assetId) return
      
      loading.value = true
      try {
        // 
        const response = await assetApi.getAssetById(assetId)
        asset.value = response.data
        
        // 
        await fetchAssetVulnerabilities()
      } catch (error) {
        console.error(':', error)
        ElMessage.error('')
      } finally {
        loading.value = false
      }
    }
    
    // 
    const fetchAssetVulnerabilities = async () => {
      try {
        const response = await assetApi.getAssetVulnerabilities(route.params.id)
        vulnerabilities.value = response.data.vulnerabilities || []
      } catch (error) {
        console.error(':', error)
        ElMessage.error('')
      }
    }
    
    // 
    const goToEdit = () => {
      router.push({ name: 'AssetEdit', params: { id: asset.value.id } })
    }
    
    // 
    const confirmDelete = () => {
      ElMessageBox.confirm(
        ` "${asset.value.name}" `,
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      ).then(() => {
        deleteAsset()
      }).catch(() => {
        // 
      })
    }
    
    // 
    const deleteAsset = async () => {
      try {
        await assetApi.deleteAsset(asset.value.id)
        ElMessage.success('')
        router.push({ name: 'AssetList' })
      } catch (error) {
        console.error(':', error)
        ElMessage.error('')
      }
    }
    
    // 
    const confirmRemoveVulnerability = (vulnerability) => {
      ElMessageBox.confirm(
        ` "${vulnerability.title}" `,
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      ).then(() => {
        removeVulnerability(vulnerability.id)
      }).catch(() => {
        // 
      })
    }
    
    // 
    const removeVulnerability = async (vulnId) => {
      try {
        await assetApi.removeVulnerabilityFromAsset(route.params.id, vulnId)
        ElMessage.success('')
        await fetchAssetVulnerabilities() // 
      } catch (error) {
        console.error(':', error)
        ElMessage.error('')
      }
    }
    
    // 
    const searchVulnerabilities = async () => {
      if (!searchForm.searchTerm || searchForm.searchTerm.trim() === '') {
        ElMessage.warning('')
        return
      }
      
      searchLoading.value = true
      try {
        // APIAPI
        const params = {
          searchTerm: searchForm.searchTerm,
          limit: 50
        }
        
        console.log(':', params)
        
        // API
        const response = await vulnerabilityApi.searchVulnerabilities(params)
        console.log(':', response.data)
        
        // 
        searchResults.value = response.data.items || response.data.vulnerabilities || []
        
        // 
        searchResults.value = searchResults.value.filter(vuln => {
          // IDAPI
          const vulnId = vuln.id || vuln._id || vuln.cveId
          return !vulnerabilities.value.some(v => 
            (v.id === vulnId) || (v.cveId && v.cveId === vuln.cveId)
          )
        })
        
        if (searchResults.value.length === 0) {
          ElMessage.info('')
        }
      } catch (error) {
        console.error(':', error)
        console.error(':', error.config)
        console.error(':', error.response?.status)
        console.error(':', error.response?.data)
        if (error.request) {
          console.error(':', error.request)
        }
        ElMessage.error(`: ${error.response?.data?.message || error.message || ''}`)
      } finally {
        searchLoading.value = false
      }
    }
    
    // 
    const handleVulnerabilitySelect = (row) => {
      // IDAPI
      const vulnId = row.id || row._id || row.cveId
      const index = selectedVulnerabilities.value.findIndex(v => 
        (v.id === vulnId) || (v.cveId && v.cveId === row.cveId)
      )
      
      if (index === -1) {
        selectedVulnerabilities.value.push(row)
      } else {
        selectedVulnerabilities.value.splice(index, 1)
      }
    }
    
    // 
    const isSelectable = (row) => {
      // IDAPI
      const vulnId = row.id || row._id || row.cveId
      return !vulnerabilities.value.some(v => 
        (v.id === vulnId) || (v.cveId && v.cveId === row.cveId)
      )
    }
    
    // 
    const addSelectedVulnerabilities = async () => {
      if (selectedVulnerabilities.value.length === 0) return
      
      try {
        // 
        const promises = selectedVulnerabilities.value.map(async vuln => {
          try {
            // IDAPI
            const vulnId = vuln.id || vuln._id
            
            // 
            if (!vulnId && vuln.cveId) {
              // 
              console.log(':', vuln)
              const vulnerabilityData = {
                title: vuln.title,
                description: vuln.description || '',
                cve: vuln.cveId,
                cvss: vuln.cvss || 0,
                severity: vuln.severity || 'medium',
                remediation: vuln.remediation || vuln.solution || '',
                references: vuln.references || [],
                tags: vuln.tags || []
              }
              
              console.log(':', vulnerabilityData)
              
              // API
              const importResponse = await vulnerabilityApi.importFromVulnDatabase(vulnerabilityData)
              console.log(':', importResponse.data)
              
              const newVulnId = importResponse.data.id || importResponse.data.vulnerabilityId || importResponse.data._id
              if (newVulnId) {
                console.log('ID:', newVulnId, ':', route.params.id)
                try {
                  const relationResponse = await assetApi.addVulnerabilityToAsset(route.params.id, newVulnId)
                  console.log(':', relationResponse)
                  return relationResponse
                } catch (relationError) {
                  console.error(':', relationError.response || relationError)
                  throw relationError
                }
              } else {
                console.error('ID:', importResponse.data)
                throw new Error('ID')
              }
            } else {
              // 
              console.log(':', vulnId, ':', route.params.id)
              try {
                const relationResponse = await assetApi.addVulnerabilityToAsset(route.params.id, vulnId)
                console.log(':', relationResponse)
                return relationResponse
              } catch (relationError) {
                console.error(':', relationError.response || relationError)
                throw relationError
              }
            }
          } catch (error) {
            console.error(':', error.response || error)
            throw error // Promise.all
          }
        })
        
        await Promise.all(promises)
        ElMessage.success('')
        dialogVisible.value = false
        selectedVulnerabilities.value = []
        searchResults.value = []
        searchForm.searchTerm = ''
        
        // 
        await fetchAssetVulnerabilities()
      } catch (error) {
        console.error(':', error)
        if (error.response) {
          console.error(':', error.response.status)
          console.error(':', error.response.data)
          console.error(':', error.config)
          if (error.request) {
            console.error(':', error.request)
          }
        }
        ElMessage.error(`: ${error.response?.data?.message || error.message || ''}`)
      }
    }
    
    // 
    const showAddNoteDialog = () => {
      noteForm.content = ''
      noteDialogVisible.value = true
    }
    
    // 
    const addAssetNote = async () => {
      if (!noteForm.content || noteForm.content.trim() === '') {
        ElMessage.warning('')
        return
      }
      
      try {
        await assetApi.addAssetNote(route.params.id, noteForm.content)
        ElMessage.success('')
        noteDialogVisible.value = false
        
        // 
        await fetchAssetDetails()
      } catch (error) {
        console.error(':', error)
        ElMessage.error('')
      }
    }
    
    // 
    const attributesArray = computed(() => {
      if (!asset.value || !asset.value.attributes) return []
      
      return Object.entries(asset.value.attributes).map(([key, value]) => ({
        key,
        value: typeof value === 'object' ? JSON.stringify(value) : value
      }))
    })
    
    // 
    const getAssetTypeLabel = (type) => {
      const types = {
        server: '',
        network_device: '',
        application: '',
        database: '',
        cloud_resource: '',
        iot_device: '',
        other: ''
      }
      return types[type] || type
    }
    
    // 
    const getAssetTypeTag = (type) => {
      const tags = {
        server: 'primary',
        network_device: 'success',
        application: 'warning',
        database: 'danger',
        cloud_resource: 'info',
        iot_device: '',
        other: 'info'
      }
      return tags[type] || 'info'
    }
    
    // 
    const getEnvironmentLabel = (env) => {
      const environments = {
        production: '',
        testing: '',
        development: '',
        staging: ''
      }
      return environments[env] || env
    }
    
    // 
    const getEnvironmentTag = (env) => {
      const tags = {
        production: 'danger',
        testing: 'warning',
        development: 'success',
        staging: 'info'
      }
      return tags[env] || 'info'
    }
    
    // 
    const getSeverityLabel = (severity) => {
      const severities = {
        critical: '',
        high: '',
        medium: '',
        low: '',
        info: ''
      }
      return severities[severity] || severity
    }
    
    // 
    const getSeverityTag = (severity) => {
      const tags = {
        critical: 'danger',
        high: 'warning',
        medium: '',
        low: 'success',
        info: 'info'
      }
      return tags[severity] || 'info'
    }
    
    // 
    const getStatusLabel = (status) => {
      const statuses = {
        open: '',
        in_progress: '',
        resolved: '',
        closed: '',
        false_positive: ''
      }
      return statuses[status] || status
    }
    
    // 
    const getStatusTag = (status) => {
      const tags = {
        open: 'danger',
        in_progress: 'warning',
        resolved: 'success',
        closed: 'info',
        false_positive: ''
      }
      return tags[status] || 'info'
    }
    
    onMounted(() => {
      fetchAssetDetails()
    })
    
    return {
      loading,
      asset,
      activeTab,
      vulnerabilities,
      dialogVisible,
      searchForm,
      searchLoading,
      searchResults,
      selectedVulnerabilities,
      noteDialogVisible,
      noteForm,
      attributesArray,
      formatDate,
      fetchAssetDetails,
      goToEdit,
      confirmDelete,
      confirmRemoveVulnerability,
      searchVulnerabilities,
      handleVulnerabilitySelect,
      isSelectable,
      addSelectedVulnerabilities,
      showAddNoteDialog,
      addAssetNote,
      getAssetTypeLabel,
      getAssetTypeTag,
      getEnvironmentLabel,
      getEnvironmentTag,
      getSeverityLabel,
      getSeverityTag,
      getStatusLabel,
      getStatusTag
    }
  }
}
</script>

